.--animation-link{
	position: relative;
	a{
		display: block;
		position: relative;
		color: $black-color;
		transition: all 0.3s ease 0s;
		&::before{
			content: '';
			display: block;
			position: absolute;
			transition: all 0.3s ease 0s;
			bottom: 0;
			right: 0;
			width: 0%;
			height: 1.5px;
			background: $black-color;
		}
		&:hover{
			color: $blue-color;
			&::before{
				width: 100%;
				left: 0;
				background: $blue-color;
			}
		}
	}
}


.--margin-block{
	margin: 100px 0px 0px 0px;
}
.--title {
	font-size: 45px;
	font-weight: 600;
	position: relative;
	color: $black-color;
	@media (max-width: 500px) {
		font-size: 36px;
	}
	&::after{
		position: absolute;
		font-size: 150px;
		color: $color-text;
		left: 0;
		top: 0;
		line-height: 40px;
		font-weight: 300;
		opacity: .2;
		@media (max-width: 500px) {
			display: none;
		}
	}
}