*,
*::before,
*::after {
	box-sizing: border-box;
}

.flex {
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
}

.--just-space {
	justify-content: space-between;
}

.--just-center {
	justify-content: center;
}

.--just-end {
	justify-content: flex-end;
}

.--align-str {
	align-items: stretch;
}

.--align-center {
	align-items: center;
}

.--align-end {
	align-items: flex-end;
}

.--dir-col {
	flex-direction: column;
}

html {
	font-size: 16px;
}

html,
body {
	min-height: 100%;
	position: relative;
	font-family: 'Jost';
}

body {
	font-size: 1rem;
}
ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
	list-style: none;
	padding: 0;
}

.container {
	width: 100%;
	max-width: 1145px;
	padding: 0 15px;
	margin: 0 auto;
	@media (max-width: 1145px) {
		max-width: 1065px;
	}
	@media (max-width: 992px) {
		max-width: 960px;
	}
	@media (max-width: 768px) {
		max-width: 720px;
	}
	@media (max-width: 576px) {
		max-width: 540px;
	}
}
.wrapper{
	display: flex;
	flex-direction: column;
	flex: 1 1 auto;
	min-height: 100%;
	background: $bg-color;
}
.content{
	display: flex;
	flex-direction: column;
	flex: 1 1 auto;
}