// ==========================

.header-middle {
	margin: 0px 0px 0px 0px;
	position: fixed;
	width: 100%;
	height: 85px;
	top: 0px;
	display: flex;
	align-items: center;
	z-index: 10;
	box-shadow: 0 1px 7px $black-color;
	background: #FEF7FF;
	@media (max-width: 550px) {
		height: 70px;
	}
}
.header-menu {
	display: flex;
	justify-content: space-between;
	align-items: center;
	&__logo{
		img{
			width: 60px;
			@media (max-width: 550px) {
				width: 50px;
			}
			@media (max-width: 400px) {
				width: 40px;
			}
		}
	}

	&__list {
		display: flex;
		gap: 26px;
		position: relative;
		@media (max-width: 550px) {
			gap: 15px;
		}
		@media (max-width: 400px) {
			gap: 10px;
		}
		a{
			font-size: 20px;
			line-height: 25px;
			font-weight: 500;
			@media (max-width: 550px) {
				font-size: 18px;
			}
			@media (max-width: 400px) {
				font-size: 15px;
			}
		}
	}
	.active{
		color: $blue-color;
		&::before{
			width: 100%;
			left: 0;
			background: $blue-color;
		}
		
	}
}
.offcanvas-menu-overlay{
	display: none;
	@media (max-width: 700px) {
		display: block;
		position: fixed;
		left: 0;
		top: 0;
		background: rgba(0, 0, 0, 0.5);
		z-index: 7;
		height: 100%;
		width: 100%;
		visibility: hidden;
		transition: all 0.3s ease 0s;
	}
	
 }
 .offcanvas-menu-overlay.active {
	@media (max-width: 700px) {
		visibility: visible;
		transition: all 0.3s ease 0s;
	}
 }
// end header

// main-screen

.main-screen {
	position: relative;

}
.main-screen-text {
	position: absolute;
	z-index: 2;
	left: 50%;
	top: 35%;
	transform: translate(-50%, -50%);
	color: $bg-none;
	font-size: 33px;
	width: 1000px;
	line-height: 1.3;
	text-align: center;
	@media (max-height: 700px) {
		font-size: 25px;
		top: 45%;
	}
	@media (max-width: 1100px) {
		padding: 0 30px;
		width: 100%;
		max-width: 1000px;
	}
	@media (max-width: 550px) {
		padding: 0 20px;
		font-size: 23px;
		line-height: 1.1;
	}
}
.main-screen-image {
	position: relative;
	img{
		width: 100%;
		height: 100vh;
		object-fit: cover;
		
	}
	&::after{
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 99%;
		background: #10006d40;
	}
}
.main-screen-btn{
	position: absolute;
	right: 50%;
	z-index: 2;
	bottom: 0%;
	transform: translate(50%, -50%);
	@media (max-height: 700px) {
		transform: translate(50%, -20%);
	}
	&:hover{
		i{
			color: #fff;
			background: $black-color;
		}
		span{
			border-bottom: 5px solid $black-color;
			border-right: 5px solid $black-color;
		}
	}
	a{
		font-size: 27px;
		color: $black-color;
		@media (max-width: 550px) {
			font-size: 20px;
			i{
				padding: 10px 35px;
			}
			span{
				width: 23px;
				height: 23px;
				
			}
		}
	}
	i{
		font-style: normal;
		font-weight: 500;
		padding: 11px 50px;
		background: #fff;
		border: 2px solid $black-color;
		border-radius: 10px;
		transition: all 0.3s ease 0s;
		display: block;
		margin: 80px 0px 0px 0px;
		white-space: nowrap;
		
		
	}
  span {
		display: block;
		width: 30px;
		height: 30px;
		border-bottom: 5px solid $bg-none;
		border-right: 5px solid $bg-none;
		transform: rotate(45deg);
		margin: -16px auto;
		animation: arrow-down 2s infinite;
		@media (max-height: 570px) {
			display: none;
		}
  }
  span:nth-child(2){
		animation-delay: -0.2s;
  }
  span:nth-child(3){
		animation-delay: -0.4s;
  }
}
// end main-screen


// about
.about {
	&__row {
		display: flex;
		gap: 20px;
		@media (max-width: 770px) {
			flex-wrap: wrap;
		}
	}

	&__left {
		flex: 0 1 65%;
		@media (max-width: 770px) {
			flex: 0 1 100%;
			order: 2;
			margin: 60px 0px 0px 0px;
		}
		@media (max-width: 500px) {
			margin: 10px 0px 0px 0px;
		}
	}

	&__title {
		&::after{
			content: 'About';
		}
	}
	&__social{
		margin: 20px 0px 0px 0px;
	}
	&__social-title{
		font-size: 27px;
	}
	&__social-list{
		display: flex;
		gap: 15px;
		margin: 10px 0px 0px 0px;
		img{
			width: 35px;
			transition: all 0.3s ease 0s;
			&:hover{
				transform: scale(1.1);
			}
		}
	}
	&__text {
		margin: 35px 0px 0px 0px;
		font-size: 17px;
		@media (max-width: 500px) {
			margin: 10px 0px 0px 0px;
			font-size: 15px;
		}
		p{
			margin: 0px 0px 13px 0px;
			@media (max-width: 500px) {
				margin: 0px 0px 8px 0px;
			}
		}
		p:last-child{
			margin: 0;
		}
	}

	&__right {
		flex: 0 0 35%;
		@media (max-width: 850px) {
			flex: 0 0 45%;
		}
		@media (max-width: 770px) {
			flex: 0 1 100%;
		}
	}

	&__image {
		border: 2px solid $black-color;
		padding: 10px;
		border-radius: 20px;
		height: 100%;
		img{
			height: 100%;
			width: 100%;
			object-fit: cover;
			border-radius: 20px;
			@media (max-width: 770px) {
				height: 600px;
			}
			@media (max-width: 600px) {
				height: auto;
			}
		}
	}
}

// end about

// portfolio

.portfolio {
	&__title {
		text-align: center;
		&::after{
			content: 'Portfolio';
			left: 50%;
			transform: translate(-50%);
		}
	}
	.container{
		text-align: center;
	}
	&__upload{
		font-style: normal;
		font-weight: 500;
		padding: 15px 80px;
		background: #fff;
		border: 2px solid $black-color;
		border-radius: 10px;
		transition: all 0.3s ease 0s;
		cursor: pointer;
		color: $color-text;
		font-size: 26px;
		line-height: 1.2;
		display: block;
		margin: 60px auto 0px;
		white-space: nowrap;
		text-align: center;
		display: inline-block;
		&:hover{
			background: $black-color;
			color: #fff;
		}
	}
	.d-none{
		display: none;
	}
}
.portfolio-content {
	margin: 70px 0px 0px 0px;
	display: flex;
	gap: 30px;
	flex-wrap: wrap;
	justify-content: center;
	&__item {
		flex: 0 1 30%;
		margin: 0px 0px 20px 0px;
		@media (max-width: 650px) {
			margin: 0 0 8px 0;
			flex: 0 1 45%;
		}
		@media (max-width: 450px) {
			margin: 0 0 0px 0;
			flex: 0 1 90%;
		}
	}

	&__item-link {
		display: block;
		transition: all 0.3s ease 0s;
		&:hover{
			transform: translate(0, -10px);
			img{
				box-shadow: none;
				border-color: $black-color;
			}
			span{
				color: $black-color;
				font-weight: 500;
			}
			b{
				color: black-color;
			}
		}
		img{
			width: 100%;
			height: 500px;
			object-fit: cover;
			object-position: top;
			border-radius: 20px;
			margin: 0px 0px 8px 0px;
			display: block;
			border: 5px solid #fff;
			box-shadow: 0 0 10px $black-color;
			transition: all 0.3s ease 0s;
		}
		span{
			font-size: 23px;
			color: $color-text;
			text-align: center;
			display: block;
			transition: all 0.3s ease 0s;
		}
		b{
			font-size: 25px;
			text-align: center;
			display: block;
			color: $blue-color;
			transition: all 0.3s ease 0s;
		}
	}
}

// end portfolio

// contact-me

.contact-me {
	position: relative;
	&__bg-image {
		background: url(../img/content/20-Most-Common-Work-from-Home-Job-Titles.webp) fixed no-repeat;
		background-size: cover;
		width: 100%;
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		height: 400px;
		&::after{
			content: '';
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			left: 0;
			background: #00000070;
		}

	}
	&__title {
		text-align: center;
		color: #fff;
		margin: 0px 0px 23px 0px;
	}
	.container{
		text-align: center;
		position: relative;
		z-index: 2;
	}
	&__list {
		display: inline-flex;
		border-radius: 30px;
		padding: 15px;
		text-align: center;
		margin: 0 auto;
		justify-content: center;
		gap: 35px;
		border: 4px solid #fff;
		background: $bg-color;
		@media (max-width: 460px) {
			gap: 22px;
			padding: 10px;
		}
		@media (max-width: 400px) {
			gap: 15px;
		}
		img{
			width: 50px;
			transition: all 0.3s ease 0s;
			@media (max-width: 400px) {
				width: 37px;
			}
			&:hover{
				transform: scale(1.2);
			}
		}
	}
}

// end portfolio

// skills

.skills {
	&__title {
		text-align: center;
		&::after{
			content: 'Skills';
			left: 50%;
			transform: translate(-50%);
		}
	}

	&__content {
		display: flex;
		gap: 20px;
		flex-wrap: wrap;
		margin: 80px 0px 0px 0px;
		justify-content: center;
	}

	&__item {
		flex: 0 1 23%;
		padding: 20px 40px;
		background: #fff;
		border-radius: 13px;
		transition: all 0.3s ease 0s;
		&:hover{
			box-shadow: 0 0 10px $black-color;
			transform: translate(0, -5px);
		}
		@media (max-width: 1050px) {
			flex: 0 1 47%;
		}
		@media (max-width: 550px) {
			flex: 0 1 100%;
		}
	}

	&__image {
		text-align: center;
		img{
			width: 80px;
			height: 80px;
			object-fit: contain;
		}
	}

	&__text {
		text-align: center;
		font-size: 26px;
		font-weight: 600;
		line-height: 1.3;
		margin: 10px 0px 0px 0px;
		color: $black-color;
	}
}


// end skills

// footer

.footer {
	background: $black-color;
	&__top {
		padding: 35px 0px;
		margin: 0px 0px 20px 0px;
		background: $bg-color;
		border-radius: 0 0 30px 30px;
		@media (max-width: 430px) {
			padding: 20px 0;
		}
	}

	&__list {
		display: flex;
		justify-content: center;
		gap: 20px;
		@media (max-width: 430px) {
			gap: 15px;
		}
		img{
			width: 50px;
			transition: all 0.3s ease 0s;
			@media (max-width: 430px) {
				width: 35px;
			}
			&:hover{
				transform: scale(1.2);
			}
		}
	}

	&__bottom {
		text-align: center;
		color: #fff;
		font-size: 18px;
		line-height: 1.2;
		padding: 0px 0px 35px 0px;
	}
}



// end footer